* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Nunito', sans-serif;
  background: #222831;
  color: white;
}

img {
  max-width: 100%;
}

.container {
  width: 1300px;
  margin: 0 auto;
}

.logIn__logo {
  width: 100px;
  margin: 20px;
}

.logIn__form {
  position: relative;
  width: 350px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  background-color: white;
  box-shadow: 0px 10px 25px rgba(29, 52, 54, 0.08);
  border-radius: 10px;

}

.logIn__title {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.02em;
  color: #29A19C;
}

.logIn__input {
  margin-top: 20px;
  padding: 10px 15px;
  width: 100%;
  border: 1px solid rgba(40, 40, 70, 0.1);
  border-radius: 8px;

}

.logIn__submit {
  position: relative;
  display: block;
  align-items: center;
  padding-top: 10px;
  margin-top: 30px;
  width: 98px;
  height: 42px;
  background: #29A19C;
  border-radius: 8px;
  border: none;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.01em;
  color: #FAFAFA;
  cursor: pointer;
  text-decoration: none;
  left: 50%;
  transform: translateX(-50%);
}

.logIn__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.logIn__text {
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.01em;
  color: #282846;
  opacity: 0.6;
}

.logIn__btns {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.logIn__btn {
  background-color: none;
  border: none;
  margin: 0 10px;
  cursor: pointer;
}
.logIn__error
{
  position: absolute;
  top: 150px;
  left: 50%;
  transform: translateX(-50%);
}
.logIn__error p
{
  color: red;
  font-size: 25px;
  font-weight: bold;
}
.main {
  display: flex;
}

.navBar {
  position: relative;
  width: 231px;
  min-height: 100vh;
  background: #2C3440;
  padding: 15px 0 15px 20px;
}

.navBar__logo {
  width: 100px;
  max-height: 45px;
}

.navBar__title {
  margin: 60px 0 20px;
  font-weight: 600;
  font-size: 24px;
  line-height: 33px;
  letter-spacing: 0.03em;
  color: #29A19C;
}

.exit {
  position: absolute;
  bottom: 20px;
  left: 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.025em;
  color: #F9F9F9;
  border: none;
  background: none;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.exit img {
  margin-right: 10px;
}

.content {
  flex: 100%;
  padding: 20px 70px 36px;
}

.header {

  display: flex;
  justify-content: space-between;
  position: relative;

}

.header__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 25px;
  width: 186px;
  height: 42px;
  background: #29A19C;
  border-radius: 8px;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.01em;
  color: #FAFAFA;
  cursor: pointer;
  border: none;
}

.header__btn img {
  margin-right: 10px;
}

.header__block {
  display: flex;
  align-items: center;
}

.header__text {
  margin-right: 15px;
  font-size: 16px;
  line-height: 22px;
  color: #F9F9F9;
}

.header__menubtn {
  display: flex;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
}

.header__avatar {
  margin-right: 15px;
}

.header__miniblock {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  background: #2C3440;
  border-radius: 5px;
}

.menu {
  width: 100%;
}

.menu__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  text-decoration: none;
  margin-top: 15px;
}

.menu__block {
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu__block p {
  margin-left: 10px;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.025em;
  color: #F9F9F9;
}
.burger__block
{
  display: flex;
  justify-content: start;
  align-items: center;
}
.burger__block p {
  margin-left: 10px;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.025em;
  color: black;
}
.menu__link-active {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 15px;
  text-decoration: none;
}

.menu__block .menu__block-2 {
  display: none;
}

.menu__link-active .menu__block-2 {
  display: block;
  width: 30px;
  height: 18px;
  background: #29A19C;
  border-radius: 10px 0px 0px 10px;
}

.content__container {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.leftColomp {
  width: 520px;

}

.rightColomp {
  width: 420px;

}

.bg {
  padding: 20px;
  background: #2C3440;
  box-shadow: 0px 10px 25px rgba(29, 52, 54, 0.08);
  border-radius: 10px;
}

.main__title {
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.02em;
  color: #29A19C;
}

.success__block {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.success__colomp {
  width: 100px;
  text-align: center;
}

.success__subtitle {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.02em;
  color: #F9F9F9;
}

.success__text {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.02em;
  color: #F9F9F9;
}

.success__number {
  font-size: 36px;
  line-height: 49px;
  letter-spacing: 0.02em;
  color: #29A19C;
}

.success__miniblock {
  margin-top: 10px;
  padding-top: 15px;
  min-height: 100px;
  background: #2C3440;
  border: 1px solid #29A19C;
  border-radius: 50%;
}

.tasks {
  margin-top: 20px;
}


.taskItem__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  padding: 12px 15px;
  max-height: 45px;
  background: #2C3440;
  border: 1px solid rgba(249, 249, 249, 0.2);
  border-radius: 10px;
  transition: 2s;
}

.taskItem__miniblock {
  display: flex;
  align-items: center;
  transition: 2s;
}

.taskItem__delete {
  background: none;
  border: none;
  cursor: pointer;
  transition: 2s;
}

.taskItem__input {
  transition: 2s;
}

.taskItem__text {
  margin-left: 5px;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #F9F9F9;
  transition: 2s;
}

.taskItem__vanish {
  transition: 2s;
  display: none;
}

.taskItem__block:hover .taskItem__vanish {
  display: block;
}

.taskComleted p {
  /* font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #F9F9F9;
  text-decoration: line-through;
  margin: 10px 0;
  padding: 12px 15px;
  background: #2C3440;
  border: 1px solid rgba(249, 249, 249, 0.2);
  border-radius: 10px; */
  text-align: center;
}

.taskItem p {
  text-align: center;
}

.taskItem__text-comleted {
  text-decoration: line-through;
}

.timer__block {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

/* .timer__colomp
{
  margin-right: 50px;
} */
.timer__subtitle {
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #F9F9F9;
}

.timer__miniblock {
  display: flex;
  justify-content: start;
  align-items: center;
  margin-top: 5px;
}

.timer__text {
  margin-left: 10px;
  font-weight: 500;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: 0.02em;
  color: #F9F9F9;
}

.observation {
  margin-top: 20px;
}

.observation__text {
  margin-top: 10px;
  font-weight: 200;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.02em;

  color: #F9F9F9;
}

.fact {
  margin-top: 20px;
}

.menuClose {
  display: none;
}

.menuOpen {
  position: absolute;
  right: 0;
  top: 60px;
  width: 200px;
  min-height: 150px;
  background-color: #222831;
  border: 2px solid #29A19C;
  border-radius: 10px;
  padding: 20px;
}

.menuOpen__btn {
  display: flex;
  align-items: center;
  background: none;
  border: none;
  margin-top: 15px;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #F9F9F9;
  cursor: pointer;
  text-decoration: none;
}

.menuOpen__btn img {
  margin-right: 10px;
}

.menuOpen__btn-premium {
  color: #29A19C;
}

.account__left {


  width: 520px;
}

.account__left-1 {
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 1;
}

.account__right {
  width: 420px;
}

.account__content {
  width: 250px;
}

/* .account__label-1 {
  margin-top: 20px;
} */

.account__input {
  margin-top: 10px;

  padding: 5px 10px;
  background: #808080;
  border: 1px solid #282846;
  border-radius: 6px;
  color: white;
  font-size: 14px;
  line-height: 19px;
}

.premiumBtn {
  width: 100%;
  padding: 10px 25px;
  margin-bottom: 20px;
  background: #ECCA75;
  border-radius: 8px;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.01em;
  color: #FAFAFA;
}

.account__btn {
  background: none;
  border: none;
  cursor: pointer;
  margin-top: 10px;
  margin-right: 20px;
  margin-bottom: 20px;
}

.account__save {
  padding: 10px 25px;
  background: #29A19C;
  border-radius: 8px;
  border: none;
  letter-spacing: 0.01em;
  color: #FAFAFA;
  cursor: pointer;
}

.account__ava img {
  margin-left: 20px;
}

.modalOpen {
  position: absolute;
  width: 100%;
  min-height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 5;

}

.modalClose {
  display: none;
}

.modal__window {
  width: 500px;
  min-height: 300px;
  margin: 100px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  opacity: 1.5;
  color: black
}

.modal__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  letter-spacing: 0.02em;
  color: #29A19C;
}

.modal__text {
  margin-top: 20px;
  font-weight: 700;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #282846;
}

.modal__input {
  width: 100%;
  margin-top: 10px;
  padding: 8px 15px;
  border: 1px solid rgba(40, 40, 70, 0.3);
  border-radius: 8px;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #000000;
}

.modal__select {
  margin-top: 10px;
  padding: 8px 15px;
  border: 1px solid rgba(40, 40, 70, 0.3);
  border-radius: 8px;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #000000;
}

.modal__btns {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.modal__btn {
  padding: 8px 25px;
  border: none;
  border-radius: 8px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.01em;
  color: #FAFAFA;
  cursor: pointer;
}

.modal__btn-cancel {
  background: #F05454;
}

.modal__btn-add {
  background: #29A19C;
}

.another {
  width: 100%;
}

.statistics__block {
  justify-content: space-around;
  margin-top: 5px;
  margin-bottom: 15px;
}

.another__miniblock {
  padding-top: 10px;
}

another__miniblock__text {
