@media (max-width: 1350px) {
  .container {
    width: 1000px;
  }

  .content {
    padding: 20px 30px 36px;
  }

  .leftColomp {
    width: 380px;
  }

  .rightColomp {
    width: 350px;
  }

  .timer__block {
    margin-top: 10px;
    flex-wrap: wrap;
  }

  .timer__colomp {
    margin-top: 5px;
  }

  .account__left {
    width: 380px;
  }

  .account__right {
    width: 350px;
  }

  .account__ava {
    width: 100px;
  }

  .account__content {
    width: 200px;
    transition: 0.9s;
  }
}

@media (max-width: 1000px) {
  .container {
    width: 100%;
  }

  .main {
    flex-wrap: wrap;
  }

  .navBar {
    width: 100%;
    min-height: auto;
    display: flex;
    justify-content: space-between;
  }

  .menu {
    display: none;
  }

  .exit {
    display: none;
  }

  .content__container {
    flex-wrap: wrap;
  }
  .infoTitle
  {
    display: block;
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
    font-size: 34px;
    color: #29A19C;
    text-transform: uppercase;
  }

  .header__block-hide {
    display: none;
  }

  .burgerMenu {
    display: block;
  }

  .leftColomp {
    width: 50%;
  }

  .rightColomp {
    width: 48%;

  }
}
.doneGif
{
  position: relative;
  width: 80px;
  left: 50%;
  top: 250px;
  transform: translateX(-50%);
  border-radius: 50%;
}

@media (max-width: 800px) {
  .content__container {
    justify-content: center;
  }

  .leftColomp {
    width: 500px;
  }

  .rightColomp {
    width: 500px;
    margin-top: 20px;
  }

  .account__left {
    position: relative;
    width: 500px;
  }

  .account__right {
    width: 500px;
  }

  .taskItem__vanish {
    display: block;
  }

  .header {
    justify-content: center;
  }
}

@media (max-width: 530px) {
  .header {
    flex-wrap: wrap;
    justify-content: center;
  }

  .leftColomp {
    width: 98%;
  }

  .rightColomp {
    width: 98%;
    margin-top: 20px;
  }

  .account__left {
    width: 98%;
  }

  .account__right {
    width: 98%;
  }

  .statistics__block {
    justify-content: center;
    flex-wrap: wrap;
  }

  .statistics__block .success__colomp {
    margin: 10px 5px;
  }

  .modal__window {
    width: 80%;
  }
}

@media (max-width: 450px) {
  .success__colomp {
    width: 80px;

  }

  .success__miniblock {
    padding-top: 10px;
    min-height: 80px;
  }

  .success__number {
    font-size: 30px;
    line-height: 35px;
  }

  .account__left-1 {
    justify-content: center;
    flex-wrap: wrap;
   
    
  }

  .account__ava img {
    margin: 0;
  }
  .logIn__form
  {
    width: 80%;
  }
}